*{margin:0;font-family:'Montserrat', sans-serif}.container{margin:0 auto;max-width:1170px;padding:0 20px}

*{margin:0;font-family:'Montserrat', sans-serif}.container{margin:0 auto;max-width:1170px;padding:0 20px}.mainLinks{font-family:'Montserrat';font-weight:600;font-size:16px;line-height:20px;text-transform:capitalize;color:#fff;text-decoration:none}

*{margin:0;font-family:'Montserrat', sans-serif}.container{margin:0 auto;max-width:1170px;padding:0 20px}.mainButton{padding:8px 40px;font-family:'Montserrat';font-weight:700;font-size:15px;color:#fff;text-transform:uppercase;background-color:#75E169;border:2px solid #75E169;border-radius:5px;cursor:pointer;transition:0.3s border}.mainButton:hover{border:2px solid #fff}

*{margin:0;font-family:'Montserrat', sans-serif}.container{margin:0 auto;max-width:1170px;padding:0 20px}.navigation{background-color:#67AEFC}.navigation__wrapper{padding:18px 0;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.navigation__logo{height:59px}.navigation__listAndButtonWrapper{display:-webkit-flex;display:flex;grid-gap:30px;gap:30px}.navigation__list{padding:0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;grid-gap:36px;gap:36px;list-style:none}



